<template>
  <v-app>
    <v-container>
      <h2 class="center-text">Retreat Page for {{retreatMenuName}}</h2>
      {{retreat}}
    </v-container>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      message: "Retreat Page",
    }
  },
  computed: {
    retreat: function () { return this.$store.getters.retreat }
  },
  created(){
    this.retreatMenuName = this.$route.params.retreat
    this.$store.dispatch('getRetreat', this.retreatMenuName)
  }
}
</script>

